.data {
  display: flex;
  gap: 50px;
  justify-content: space-between;
  align-items: flex-start;
  padding: 50px 0;

  img {
    max-width: 293px;
    width: 100%;
    max-height: 450px;
  }

  a {
    font-size: 18px;
    line-height: 22px;
    font-weight: 700;

    &:hover,
    &:focus {
        color: $main-color;
    }
  }

  ul {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
}

.data__text {
  display: flex;
  flex-direction: column;
  gap: 25px;
  max-width: 550px;
  width: 100%;

  h1 {
    font-size: 22px;
    line-height: 26px;
    font-weight: 700;
  }

  p {
    font-size: 18px;
    line-height: 22px;
  }

  span {
    font-size: 24px;
    line-height: 29px;
    font-weight: 700;
    color: #9f0013;
  }
}

.data__links {
  display: flex;
  gap: 24px;

  a:hover,
  a:focus {
      color: #ffffff;
  }
}
